import React from 'react';
import {
  Document,
  Page,
  Text,
  StyleSheet,
  Font,
  View,
} from '@react-pdf/renderer';
import robotoNormal from '../../../fonts/Roboto-Regular.ttf';
import CvTitle from '../../cv-title/cv-title.component';
import CvProfile from './../../cv-pdf-profile/cv-pdf-profile.component';

const CvTemplateOne = () => {
  return (
    <Document title="LD-CV" author="LDK">
      <Page style={styles.container} size="A4">
        <View style={styles.row}>
          <View style={styles.col}>
            <CvProfile />
            <View style={styles.section}>
              <CvTitle title="Mc tiu ngh nghip" />
              <View>
                <Text>
                  {`
                Khi c nhn vo cng ty em mong mun:
                + ng dng kin thc lp
                trnh web  hc vo mi trng lm vic thc t. 
                + Thu thp kin
                thc mi, mong c hc hi t cng ty  pht trin bn thn cng
                nh lm vic  cng ty sau ny.
                + Mun pht trin theo hng
                full-stack. 
                + Tri nghim mi trng lm vic chuyn nghip.
                  `}
                </Text>
              </View>
            </View>
            <View>
              <CvTitle title="Ngi tham chiu" />
              <View>
                <Text>
                  {`
              Anh: LDK - Trng phng Maketing
              Cng ty: Top CV
              in thoi: 0914078960
                  `}
                </Text>
              </View>
            </View>
          </View>
          <View style={styles.col}>
            <View style={styles.section}>
              <CvTitle title="Hc Vn" />
              <View>
                <Text>10/2017 - Hin ti</Text>
                <Text style={styles.s1}>i hc Bch Khoa H Ni</Text>
                <Text style={styles.s2}>Chuyn ngnh: Cng ngh thng tin</Text>
                <Text>CPA: 2.5</Text>
              </View>
            </View>
            <View style={styles.section}>
              <CvTitle title="KINH NGHIM LM VIC" />
              <View>
                <View>
                  <Text>10/2017 - Hin ti</Text>
                  <Text style={styles.s1}>i hc Bch Khoa H Ni</Text>
                  <Text style={styles.s2}>Game Ma Si</Text>
                  <Text>{`
                Front-end: Reactjs
                Back-end: Nodejs
                Database: Mongodb
                Github: https://github.com/khanhleemtp/ld_game
                `}</Text>
                </View>
                <View>
                  <Text>10/2017 - Hin ti</Text>
                  <Text style={styles.s1}>i hc Bch Khoa H Ni</Text>
                  <Text style={styles.s2}>
                    Lm trang web quiz app: https://five-animals.herokuapp.com/
                  </Text>
                  <Text>{`
                  Front-end: Reactjs.
                  Back-end: Nodejs.
                  Github: https://github.com/khanhleemtp/animals-quiz-app
                `}</Text>
                </View>
              </View>
            </View>
            <View style={styles.section}>
              <CvTitle title="Hot ng" />
              <View>
                <Text>10/2017 - Hin ti</Text>
                <Text style={styles.s2}>V tr tham gia: Sinh Vin</Text>
                <Text>S kin: SOICT Innovation 2019</Text>
              </View>
            </View>
            <View style={styles.section}>
              <CvTitle title="Chng ch" />
              <View>
                <Text style={styles.s3}>Gii nht Ti nng TOPCV</Text>
                <Text style={styles.s2}>2013</Text>
              </View>
            </View>
            <View style={styles.section}>
              <CvTitle title="Gii thng" />
              <View>
                <Text style={styles.s3}>
                  Nhn vin xut sc nm cng ty TOPCV
                </Text>
                <Text style={styles.s2}>2014</Text>
              </View>
            </View>
            <View style={styles.section}>
              <CvTitle title="Thng tin thm" />
              <View>
                <Text>
                  Khi xin vo cng ty em mong mun c hc hi, pht trin bn
                  thn v lm vic  cng ty sau ny
                </Text>
              </View>
            </View>
          </View>
        </View>

        <Text
          style={styles.pageNumber}
          render={({ pageNumber, totalPages }) =>
            `${pageNumber} / ${totalPages}`
          }
          fixed
        />
      </Page>
    </Document>
  );
};

Font.register({
  family: 'Roboto',
  src: robotoNormal,
  fontWeight: 'normal',
});

// Create style with font-family

const styles = StyleSheet.create({
  container: {
    paddingTop: 30,
    paddingBottom: 65,
    paddingHorizontal: 30,
    fontFamily: 'Roboto',
    fontSize: 12,
    color: '#666',
  },
  row: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  col: {
    width: '48%',
  },
  name: {
    fontSize: 24,
    marginVertical: 10,
    color: '#1a936f',
  },
  avatarContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  avatar: {
    border: 2,
    width: 120,
    height: 120,
    borderRadius: 120 / 2,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderColor: '#1a936f',
  },
  contact: {
    display: 'flex',
    width: '100%',
    flexWrap: 'nowrap',
    alignItems: 'center',
    justifyContent: 'space-between',
    flexDirection: 'row',
  },
  text: {
    textTransform: 'uppercase',
    fontSize: 14,
    marginRight: 16,
    fontWeight: 'extrabold',
  },
  line: {
    borderTop: 1,
    borderStyle: 'solid',
    borderColor: '#1a936f',
    flexGrow: 1,
  },

  image: {
    width: 110,
    height: 110,
    borderRadius: 55,
  },

  pageNumber: {
    position: 'absolute',
    fontSize: 12,
    bottom: 30,
    left: 0,
    right: 0,
    textAlign: 'center',
    color: 'grey',
  },
  section: {
    marginBottom: 10,
  },
  s1: {
    fontSize: 13,
    color: '#1a936f',
    fontWeight: 'bold',
  },
  s2: {
    fontWeight: 'bold',
  },
  s3: {
    color: '#1a936f',
  },
});

export default CvTemplateOne;
